<template>
  <div class="home">
    <el-row>
      <!-- 左 -->
      <el-col :span="8" style="padding-right: 10px">
        <!-- 用户信息及登录信息展示 -->
        <el-card class="box-card">
          <div slot="header" class="user">
            <img :src="`./userImgs/${userImg}.jpg`" alt="" />
            <div class="user-info">
              <p class="name">{{ nickName }}</p>
              <p class="access">
                {{ userImg == "user1" ? "超级管理员" : "普通用户" }}
              </p>
            </div>
          </div>
          <div class="login-info">
            <p>
              上次登录时间
              <span
                >{{ new Date().getFullYear() }}-{{
                  new Date().getMonth() + 1
                }}-{{ new Date().getDate() }}</span
              >
            </p>
            <p>
              上次登录地点 <span>{{ loginAddr }}</span>
            </p>
          </div>
        </el-card>
        <!-- 表格信息 -->
        <el-card class="box-card" style="margin-top: 20px; height: 470px">
          <el-table :data="tableData" stripe style="width: 100%" height="460">
            <el-table-column prop="name" label="品牌" width="75">
            </el-table-column>
            <el-table-column prop="daySell" label="今日购买" width="80">
            </el-table-column>
            <el-table-column prop="monthSell" label="本月购买" width="80">
            </el-table-column>
            <el-table-column prop="allSell" label="总购买量" width="80">
            </el-table-column>
          </el-table>
        </el-card>
      </el-col>
      <!-- 右 -->
      <el-col :span="16" style="padding-left: 10px">
        <!-- 右上角六个小卡片 -->
        <div class="num">
          <el-card
            v-for="item in countData"
            :key="item.name"
            :body-style="{ display: 'flex', padding: 0 }"
          >
            <i
              :class="`el-icon-${item.icon}`"
              class="icon"
              :style="{ background: item.color }"
            ></i>
            <div class="detail">
              <p class="price">{{ item.value }}</p>
              <p class="desc">{{ item.name }}</p>
            </div>
          </el-card>
        </div>
        <!-- 折线图 -->
        <el-card style="height: 280px">
          <div id="line-chart" style="height: 280px"></div>
        </el-card>
        <!-- 饼图和柱状图 -->
        <div class="graph">
          <el-card style="height: 260px">
            <div id="bar-chart" style="height: 260px"></div>
          </el-card>
          <el-card style="height: 260px">
            <div id="pie-chart" style="height: 230px"></div>
          </el-card>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import { getData } from "@/api/index.js";
import * as echarts from "echarts";
import Cookie from "js-cookie";
export default {
  data() {
    return {
      tableData: new Array(),
      countData: new Array(),
      orderData: new Array(),
      userData: new Array(),
      videoData: new Array(),
      nickName: Cookie.get("nickName"),
      loginAddr: Cookie.get("loginAddr"),
    };
  },
  computed: {
    userImg() {
      return Cookie.get("userImg");
    },
  },
  mounted() {
    // 从接口解析数据并赋值
    getData().then((data) => {
      const allData = data.data.data;
      this.tableData = allData.tableData;
      this.countData = allData.countData;

      // 使用echarts进行图表绘制
      // 折线图
      let lineChartArea = document.getElementById("line-chart");
      let lineChart = echarts.init(lineChartArea); // 初始化echart实例
      let labels = Object.keys(allData.orderData.data[0]);
      let yData = [];
      labels.map((key) => {
        yData.push({
          name: key,
          type: "line",
          data: allData.orderData.data.map((item) => item[key]),
        });
      });
      let option1 = {
        title: {},
        // 放在指定点上会显示具体数据
        tooltip: {},
        // 图例
        legend: {
          data: labels,
        },
        // x轴
        xAxis: {
          data: allData.orderData.date,
        },
        yAxis: {},
        // series 是一个数组，每个元素表示一条完整的折线
        series: yData,
      };
      // 将数据渲染到指定位置
      lineChart.setOption(option1);

      // 柱状图
      let barChartArea = document.getElementById("bar-chart");
      let barChart = echarts.init(barChartArea); // 初始化echart实例
      let barData = allData.userData;
      let option2 = {
        legend: {
          textStyle: {
            color: "#333",
          },
        },
        grid: {
          left: "20%",
        },
        // 提示框
        tooltip: {},
        xAxis: {
          type: "category",
          data: barData.map((item) => item.date),
          axisLine: {
            lineStyle: {
              color: "#17b3a3",
            },
          },
        },
        axisLabel: {
          interval: 0,
          color: "#333",
        },
        yAxis: [
          {
            type: "value",
            data: [],
            axisLine: {
              lineStyle: {
                color: "#17b3a3",
              },
            },
          },
        ],
        color: ["#2ec7c9", "#b6a2de"],
        series: [
          {
            name: "新增用户",
            data: barData.map((item) => item.new),
            type: "bar",
          },
          {
            name: "活跃用户",
            data: barData.map((item) => item.active),
            type: "bar",
          },
        ],
      };
      barChart.setOption(option2);

      // 饼图
      let pieChartArea = document.getElementById("pie-chart");
      let pieChart = echarts.init(pieChartArea); // 初始化echart实例
      let pieData = allData.videoData;
      let option3 = {
        tooltip: {},
        color: [
          "#0f78f4",
          "#dd536b",
          "#9462e5",
          "#a6a6a6",
          "#e1bb22",
          "#39c362",
          "#3ed1cf",
        ],
        series: [
          {
            data: pieData,
            type: "pie",
          },
        ],
      };
      pieChart.setOption(option3);
    });
  },
};
</script>

<style lang="less">
.home {
  // el-main高度自适应设置
  overflow-y: auto;
  height: 100%;
}
.user {
  display: flex;
  align-items: center;

  img {
    margin-right: 40px;
    width: 150px;
    height: 150px;
    border-radius: 50%;
  }

  .user-info {
    .name {
      font-size: 20px;
      font-weight: 800;
      margin-bottom: 10px;
    }

    .access {
      color: #999;
    }
  }
}

.login-info {
  p {
    line-height: 24px;
    font-size: 14px;
    color: #999;
    span {
      color: #666;
      margin-left: 10px;
    }
  }
}

.num {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  .icon {
    height: 80px;
    width: 80px;
    font-size: 30px;
    text-align: center;
    line-height: 80px;
  }

  .detail {
    margin-left: 15px;
    display: flex;
    flex-direction: column;
    justify-content: center;

    .price::before {
      content: "￥";
    }

    .price {
      font-size: 30px;
      margin-bottom: 10px;
      line-height: 30px;
      height: 30px;
    }
    .desc {
      font-size: 14px;
      color: #999;
      text-align: center;
    }
  }

  .el-card {
    width: 32%;
    margin-bottom: 20px;
  }
}

.graph {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;

  .el-card {
    width: 48%;
  }
}
</style>