<template>
  <div class="login">
    <el-form
      :model="form"
      :rules="rules"
      label-width="70px"
      :inline="true"
      class="login-container"
      ref="form"
    >
      <h3>后台管理系统登录</h3>
      <el-form-item prop="username">
        <span slot="label">
          <span style="color: #fff">用户名</span>
        </span>
        <el-input v-model="form.username" placeholder="请输入用户名"></el-input>
      </el-form-item>
      <el-form-item prop="password">
        <span slot="label">
          <span style="color: #fff">密码</span>
        </span>
        <el-input
          v-model="form.password"
          placeholder="请输入密码"
          type="password"
          show-password
        ></el-input>
      </el-form-item>
      <el-form-item class="buttonBox">
        <el-button type="primary" @click="submit">登录</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import Mock from "mockjs";
import Cookie from "js-cookie";
import { getMenu } from "../api";
export default {
  data() {
    return {
      form: {
        username: "",
        password: "",
      },
      rules: {
        username: [
          {
            required: true,
            message: "请输入用户名",
            trigger: "blur",
            color: "#fff",
          },
        ],
        password: [{ required: true, message: "请输入密码", trigger: "blur" }],
      },
    };
  },
  methods: {
    // 登录
    submit() {
      this.$refs.form.validate((valid) => {
        if (valid) {
          getMenu(this.form).then(({ data }) => {
            if (data.code == 20000) {
              // 将token存入cookie用于不同页面间通信
              Cookie.set("token", data.data.token);
              // 获取菜单及个人信息相关数据，存入store
              this.$store.commit("setMenu", data.data);
              // 在store中动态添加路由，防止用户手动修改路由进入未被允许的页面
              this.$store.commit("addMenu", this.$router);
              console.log(this.$router);
              // 跳转到首页
              this.$router.push("/home");
            } else {
              this.$message.error(data.data.message);
            }
          });
        }
      });
    },
  },
};
</script>

<style lang="less" scoped>
.login {
  background: url("../assets/universe.jpg") no-repeat;
  background-size: 100% 100%;
  height: 100%;
  position: fixed; // 使div脱离文档流
  width: 100%;
}

.el-form-item__label {
  color: #fff;
}

.login-container {
  width: 350px;
  border: 1px solid #eaeaea;
  margin: 150px auto;
  padding: 35px 35px 15px 35px;
  box-sizing: border-box;
  border-radius: 15px;
  box-shadow: 0 0 25px #cac6c6;
  backdrop-filter: blur(10px);

  h3 {
    text-align: center;
    margin-bottom: 40px;
    color: #fff;
  }

  /deep/ .el-form-item__error {
    color: #fff;
  }

  .el-input {
    width: 198px;
  }

  .buttonBox {
    display: flex;
    justify-content: center;
    .el-button {
      margin-top: 10px;
      width: 100px;
    }
  }
}
</style>