// 用户信息相关接口模拟
import Mock from 'mockjs';

function param2Obj(url) {
    const search = url.split('?')[1];
    if (!search) {
        return {};
    }
    return JSON.parse(
        '{"' + decodeURIComponent(search)
            .replace(/"/g, '\\"')
            .replace(/&/g, '","')
            .replace(/=/g, '":"')
        + '"}'
    );
}

let List = [];
const count = 300;

for (let i = 0; i < count; i++) {
    List.push(
        Mock.mock({
            id: Mock.Random.guid(),
            name: Mock.Random.cname(),
            addr: Mock.mock('@county(true)'),
            'age|18-60': 1,
            birth: Mock.Random.date(),
            gender: Mock.Random.integer(0, 1) // 此处规定0女1男
        })
    )
}

export default {
    // 获取列表
    getUserList: config => {
        const { name, page = 1, limit = 20 } = param2Obj(config.url);
        const mockList = List.filter(user => {
            if (name && user.name.indexOf(name) == -1 && user.addr.indexOf(name) == -1) {
                return false;
            }
            return true;
        })
        const pageList = List.filter((item, index) => index < limit * page && index >= limit * (page - 1));
        return {
            code: 20000,
            count: mockList.length,
            countPerPage: limit,
            List: name ? mockList : pageList
        }
    },

    // 增加用户
    createUser: config => {
        const { name, addr, age, birth, gender } = JSON.parse(config.body);
        List.unshift({
            id: Mock.Random.guid(),
            name, addr, age, birth, gender
        })
        return {
            code: 20000,
            data: {
                message: "添加成功"
            }
        }
    },

    // 删除用户
    deleteUser: config => {
        const { id } = JSON.parse(config.body);
        if (!id) {
            return {
                code: -999,
                message: "参数不正确"
            }
        }
        else {
            List = List.filter(u => u.id !== id);
            return {
                code: 20000,
                message: "删除成功"
            }
        }
    },

    // 批量删除
    batchremove: config => {
        const { ids } = JSON.parse(config.url);
        ids = ids.split(',');
        List = List.filter(u => !ids.includes(u.id));
        return {
            code: 20000,
            data: {
                message: "批量删除成功"
            }
        }
    },

    // 修改用户信息
    updateUser: config => {
        let { id, name, addr, age, birth, gender } = JSON.parse(config.body);
        gender = parseInt(gender);
        List.some(u => {
            if (u.id == id) {
                u.name = name;
                u.addr = addr;
                u.age = age;
                u.birth = birth;
                u.gender = gender;
                return true;
            }
        });
        return {
            code: 20000,
            data: {
                message: "修改成功"
            }
        }
    }
}