<template>
  <div class="manage">
    <div class="manage-header">
      <!-- 搜索框 -->
      <div class="searchBox">
        <el-input
          v-model="userform.operation"
          placeholder="请输入操作人或操作方式"
        ></el-input>
        <el-button @click="onSearch" icon="el-icon-search" type="primary"
          >查询</el-button
        >
      </div>
    </div>
    <!-- 表格 -->
    <el-table
      :data="tableData"
      style="width: 100%"
      height="95%"
      stripe
      :header-cell-style="{ 'text-align': 'center' }"
      :cell-style="{ 'text-align': 'center' }"
    >
      <el-table-column prop="time" label="操作时间"> </el-table-column>
      <el-table-column prop="person" label="操作人"> </el-table-column>
      <el-table-column prop="content" label="操作内容"> </el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button size="mini" type="danger" @click="handleDelete(scope.row)"
            >删除</el-button
          >
        </template>
      </el-table-column>
    </el-table>
    <div class="pager">
      <el-pagination
        layout="prev, pager, next"
        :total="total"
        :page-size="perPage"
        @current-change="handlerPage"
      >
      </el-pagination>
    </div>
  </div>
</template>

<script>
import { getLog, delLog } from "../api";

export default {
  data() {
    return {
      userform: {
        operation: "",
      },
      tableData: [],
      total: 0,
      perPage: 20,
      perPage: 20, // 每页的最大条目数
      nowPage: 1,
    };
  },
  methods: {
    // 获取列表数据
    getList() {
      getLog({
        params: {
          name: this.userform.operation,
          page: this.nowPage,
          limit: this.perPage,
        },
      }).then(({ data }) => {
        this.total = data.count ? data.count : 0;
        this.tableData = data.List;
      });
    },
    handleDelete(rowdata) {
      this.$confirm("此操作将永久删除该条日志, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          delLog({ id: rowdata.id }).then(() => {
            this.getList();
            this.$message({
              type: "success",
              message: "删除成功!",
            });
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },
    handlerPage(val) {
      this.nowPage = val;
      this.getList();
    },
    onSearch() {
      this.getList();
    },
  },
  mounted() {
    this.getList();
  },
};
</script>

<style lang="less" scoped>
.manage {
  height: 80%;

  .manage-header {
    display: flex;
    justify-content: space-between;
    margin-bottom: 15px;

    .searchBox {
      display: flex;
      align-items: center;
      .el-input {
        margin-right: 10px;
      }
    }
  }
}

.pager {
  display: flex;
  margin-top: 10px;
  justify-content: center;
}
</style>