// 操作日志相关接口模拟
import Mock from 'mockjs';

function param2Obj(url) {
    const search = url.split('?')[1];
    if (!search) {
        return {};
    }
    return JSON.parse(
        '{"' + decodeURIComponent(search)
            .replace(/"/g, '\\"')
            .replace(/&/g, '","')
            .replace(/=/g, '":"')
        + '"}'
    );
}

let List = [];

export default {
    // 获取日志列表
    getLogList: config => {
        const { name, page = 1, limit = 20 } = param2Obj(config.url);
        const mockList = List.filter(log => {
            if (name && log.person.indexOf(name) == -1 && log.content.indexOf(name) == -1) {
                return false;
            }
            return true;
        })
        const pageList = List.filter((item, index) => index < limit * page && index >= limit * (page - 1));
        return {
            code: 20000,
            count: mockList.length,
            countPerPage: limit,
            List: name ? mockList : pageList
        }
    },

    // 增加日志
    createLog: config => {
        const { time, person, content } = JSON.parse(config.body);
        List.unshift({
            id: Mock.Random.guid(),
            time, person, content
        })
        return {
            code: 20000,
            data: {
                message: "添加成功"
            }
        }
    },

    // 删除日志
    deleteLog: config => {
        const { id } = JSON.parse(config.body);
        if (!id) {
            return {
                code: -999,
                message: "参数不正确"
            }
        }
        else {
            List = List.filter(u => u.id !== id);
            return {
                code: 20000,
                message: "删除成功"
            }
        }
    },
}