import Mock from "mockjs";
import homeApi from './mockServerData/home';
import userApi from './mockServerData/user';
import loginApi from './mockServerData/permission';
import logApi from './mockServerData/log';


// 定义mock请求拦截
Mock.mock('/api/home/getData', homeApi.getStatisticalData);

// 用户列表数据
Mock.mock('/api/user/add', 'post', userApi.createUser);
Mock.mock('/api/user/update', 'post', userApi.updateUser);
Mock.mock('/api/user/del', 'post', userApi.deleteUser);
Mock.mock(/api\/user\/getUser/, userApi.getUserList);

// 登录数据
Mock.mock(/api\/permission\/getMenu/, 'post', loginApi.getMenu);

// 日志列表数据
Mock.mock('/api/log/add', 'post', logApi.createLog);
Mock.mock('/api/log/del', 'post', logApi.deleteLog);
Mock.mock(/api\/log\/getLog/, logApi.getLogList);