import Cookie from "js-cookie";

export default {
    // state数据在页面刷新后会丢失，需要进行数据持久化处理
    state: {
        isCollapse: false, // 用于控制侧边栏展开还是收起
        tabslist: [
            // 面包屑数据
            {
                path: "/",
                name: "home",
                label: "首页",
                icon: "s-home",
                url: "Home/Home",
            }
        ],
    },
    mutations: {
        // 修改菜单展开还是收起的方法
        collapseMenu(state) {
            state.isCollapse = !state.isCollapse;
        },
        // 更新面包屑数据
        // state表示当前state对象，item为点击侧边栏是传的参item
        selectMenu(state, item) {
            // 判断添加的数据是否为首页
            if (item.name == 'home') {
                return;
            }
            const index = state.tabslist.findIndex(nowElement => nowElement.name == item.name);
            if (index === -1) {
                state.tabslist.push(item);
            }
        },
        // 设置菜单和个人信息
        setMenu(state, data) {
            // 数据持久化设置
            Cookie.set('menu', JSON.stringify(data.menu));
            Cookie.set('userImg', data.image);
            Cookie.set('nickName', data.nickName);
            Cookie.set('loginAddr', data.loginAddr);
        },
        // 动态注册路由
        addMenu(state, router) {
            if (!Cookie.get('menu')) {
                return;
            }
            const menu = JSON.parse(Cookie.get('menu'));
            // 组装动态路由的数据
            const menuArr = [];
            menu.map(item => {
                if (item.children) {
                    item.children = item.children.map(subItem => {
                        subItem.component = () => import('../views/' + subItem.url);
                        return subItem;
                    })
                    menuArr.push(...item.children);
                }
                else {
                    item.component = () => import('../views/' + item.url);
                    menuArr.push(item);
                }
            })
            // 路由的动态添加
            menuArr.map(item => {
                router.addRoute('Main', item);
            })
        }
    }
}