<template>
  <div class="header">
    <div class="left">
      <!-- 点击button触发state的修改 -->
      <el-button
        icon="el-icon-menu"
        size="mini"
        @click="handlerMenu"
      ></el-button>
      <!-- 
        面包屑
        主要功能：
          类似于一个历史记录数组，点击侧边栏时，如果对应信息已在数组中或点击首页则不进行操作
          否则将记录push到数组中
          数组中的记录将渲染到面包屑位置，点击面包屑也会跳转到对应组件
        数组使用vuex进行存储，存在store/tab.js中，点击侧边栏时调用mutation中的函数更新数组
        数组信息以computed形式获取，并进行循环渲染
       -->
      <span>
        <el-breadcrumb separator="/" separator-class="el-icon-arrow-right">
          <el-breadcrumb-item
            v-for="item in tags"
            :key="item.path"
            :to="{ path: item.path }"
            >{{ item.label }}</el-breadcrumb-item
          >
        </el-breadcrumb>
      </span>
    </div>
    <div class="right">
      <el-dropdown :hide-on-click="false" @command="handleCommand">
        <span class="el-dropdown-link">
          <img :src="`./userImgs/${userImg}.jpg`" alt="" class="user" />
        </span>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item>个人中心</el-dropdown-item>
          <el-dropdown-item command="exit">退出</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>
  </div>
</template>

<script>
import Cookie from "js-cookie";
import { mapState } from "vuex";
export default {
  methods: {
    // $store是在main.js中挂载到vue实例上的
    // 此处commit即调用tab中mutations的collapseMenu方法
    handlerMenu() {
      this.$store.commit("collapseMenu");
    },
    clearCookie() {
      var keys = document.cookie.match(/[^ =;]+(?==)/g);
      if (keys) {
        for (var i = keys.length; i--; ) {
          document.cookie =
            keys[i] + "=0;expires=" + new Date(0).toUTCString() + ";max-age=0";
        }
      }
    },
    exit() {
      this.clearCookie();
      this.$router.push("/login");
    },
    handleCommand(command) {
      if (command == "exit") {
        this.exit();
      }
    },
  },
  computed: {
    ...mapState({
      tags: (state) => state.tab.tabslist,
    }),
    userImg() {
      return Cookie.get("userImg");
    },
  },
};
</script>

<style lang="less">
.header {
  background-color: #333;
  height: 60px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;

  .left {
    display: flex;
    align-items: center;
    .el-breadcrumb__inner {
      color: #fff;
    }
  }

  span {
    color: #fff;
    font-size: 14px;
    margin-left: 10px;
  }

  .user {
    margin-bottom: 20px;
    width: 40px;
    height: 40px;
    border-radius: 50%;
  }
}
</style>