<template>
  <div class="manage">
    <!-- 弹窗 -->
    <el-dialog
      :title="modelType ? '修改数据' : '新增数据'"
      :visible.sync="dialogVisible"
      width="50%"
      :before-close="beforeClose"
    >
      <!-- 
        用户的表单区域:
          rules 设置校验规则 
          每个item上设置props，与字段名称一致
       -->
      <el-form
        :inline="true"
        ref="form"
        :model="form"
        label-width="80px"
        :rules="rules"
      >
        <el-form-item label="姓名" prop="name">
          <el-input v-model="form.name" placeholder="请输入姓名"></el-input>
        </el-form-item>
        <el-form-item label="年龄" class="ageInput" prop="age">
          <el-input v-model="form.age" placeholder="请输入年龄"></el-input>
        </el-form-item>
        <el-form-item label="性别" prop="gender">
          <el-select v-model="form.gender" placeholder="请选择">
            <el-option label="男" value="1"></el-option>
            <el-option label="女" value="0"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="出生日期" prop="birth">
          <el-date-picker
            type="date"
            placeholder="选择日期"
            v-model="form.birth"
            style="width: 100%"
            value-format="yyyy-MM-DD"
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="地址" prop="addr">
          <el-input v-model="form.addr" placeholder="请输入地址"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="beforeClose">取 消</el-button>
        <el-button type="primary" @click="submit">确 定</el-button>
      </span>
    </el-dialog>
    <div class="manage-header">
      <!-- 搜索框 -->
      <div class="searchBox">
        <el-input
          v-model="userform.name"
          placeholder="请输入姓名或地址"
        ></el-input>
        <el-button @click="onSearch" icon="el-icon-search" type="primary"
          >查询</el-button
        >
      </div>
      <!-- 新增按钮 -->
      <el-button @click="handlerAddButton" type="primary">+ 新增</el-button>
    </div>
    <!-- 表格 -->
    <el-table
      :data="tableData"
      style="width: 100%"
      height="95%"
      stripe
      :header-cell-style="{ 'text-align': 'center' }"
      :cell-style="{ 'text-align': 'center' }"
    >
      <el-table-column prop="name" label="姓名"> </el-table-column>
      <el-table-column prop="gender" label="性别"> </el-table-column>
      <el-table-column prop="age" label="年龄"> </el-table-column>
      <el-table-column prop="birth" label="出生日期"> </el-table-column>
      <el-table-column prop="addr" label="地址" width="360"> </el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button size="mini" @click="handleEdit(scope.row)">编辑</el-button>
          <el-button size="mini" type="danger" @click="handleDelete(scope.row)"
            >删除</el-button
          >
        </template>
      </el-table-column>
    </el-table>
    <div class="pager">
      <el-pagination
        layout="prev, pager, next"
        :total="total"
        :page-size="perPage"
        @current-change="handlerPage"
      >
      </el-pagination>
    </div>
  </div>
</template>

<script>
import Cookie from "js-cookie";
import { getUser, addUser, editUser, delUser, addLog } from "../api";

export default {
  data() {
    return {
      dialogVisible: false,
      form: {
        name: "",
        age: "",
        gender: "",
        birth: "",
        addr: "",
      },
      rules: {
        name: [
          { required: true, message: "请输入姓名", trigger: "blur" },
          { min: 2, max: 4, message: "长度在 2 到 4 个字符", trigger: "blur" },
        ],
        age: [{ required: true, message: "请输入年龄", trigger: "blur" }],
        gender: [{ required: true, message: "请选择性别", trigger: "blur" }],
        birth: [{ required: true, message: "请选择出生日期", trigger: "blur" }],
        addr: [{ required: true, message: "请输入地址", trigger: "blur" }],
      },
      tableData: [],
      modelType: 0, // 0表示新增弹窗，1表示修改弹窗
      total: 0, // 当前总条数
      perPage: 20, // 每页的最大条目数
      nowPage: 1,
      userform: {
        name: "",
      },
    };
  },
  methods: {
    // 提交用户表单
    submit() {
      // valid表示表单是否通过校验
      this.$refs.form.validate((valid) => {
        if (valid) {
          if (this.modelType) {
            // 修改数据的操作：修改后调用getList更新数据
            editUser(this.form).then(() => {
              this.getList();
              addLog({
                time: `${new Date().toLocaleDateString()} ${new Date().toLocaleTimeString()}`,
                person: Cookie.get("nickName"),
                content: `修改用户${this.form.name}的信息`,
              });
            });
          } else {
            // 新增数据的操作: 新增后调用getList更新数据
            addUser(this.form).then(() => {
              this.getList();
            });
            addLog({
              time: `${new Date().toLocaleDateString()} ${new Date().toLocaleTimeString()}`,
              person: Cookie.get("nickName"),
              content: "新增用户：" + this.form.name,
            });
          }
          this.$refs.form.resetFields(); // 清空表单
          this.dialogVisible = false;
        }
      });
    },
    // 点击弹窗叉时的操作
    beforeClose() {
      this.$refs.form.resetFields(); // 清空表单
      this.dialogVisible = false;
    },
    handleEdit(rowdata) {
      this.modelType = 1;
      this.form = JSON.parse(JSON.stringify(rowdata));
      this.dialogVisible = true;
    },
    // 获取列表数据
    getList() {
      getUser({
        params: {
          name: this.userform.name,
          page: this.nowPage,
          limit: this.perPage,
        },
      }).then(({ data }) => {
        this.total = data.count ? data.count : 0;
        this.tableData = data.List.map((item) => {
          item.gender = item.gender ? "男" : "女";
          return item;
        });
      });
    },
    handlerAddButton() {
      this.modelType = 0;
      this.dialogVisible = true;
    },
    handleDelete(rowdata) {
      this.$confirm("此操作将永久删除该用户, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          delUser({ id: rowdata.id }).then(() => {
            this.getList();
            this.$message({
              type: "success",
              message: "删除成功!",
            });
            addLog({
              time: `${new Date().toLocaleDateString()} ${new Date().toLocaleTimeString()}`,
              person: Cookie.get("nickName"),
              content: "删除用户：" + rowdata.name,
            });
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },
    // 选择页码的回调函数，val表示页码
    handlerPage(val) {
      this.nowPage = val;
      this.getList();
    },
    onSearch() {
      addLog({
        time: `${new Date().toLocaleDateString()} ${new Date().toLocaleTimeString()}`,
        person: Cookie.get("nickName"),
        content: "查询关键词：" + this.userform.name,
      });
      this.getList();
    },
  },
  mounted() {
    this.getList();
  },
};
</script>

<style lang="less" scoped>
.manage {
  height: 80%;
}
.ageInput {
  margin-left: 15px;
}
.pager {
  display: flex;
  margin-top: 10px;
  justify-content: center;
}
.manage-header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 15px;

  .searchBox {
    display: flex;
    align-items: center;
    .el-input {
      margin-right: 10px;
    }
  }
}
</style>