// 对axios进行二次封装
import axios from 'axios';

const http = axios.create({
    // 通用请求的地址前缀
    baseURL: "/api",
    // 请求超时时间（ms）
    timeout: 10000
});

// 添加请求拦截器
http.interceptors.request.use(function (config) {
    // 发送请求前需要进行的操作
    return config;
}, function (error) {
    // 对请求错误的处理
    return Promise.reject(error);
});

// 添加响应拦截器
http.interceptors.response.use(function (response) {
    // 对响应数据进行的操作
    return response;
}, function (error) {
    // 对响应错误的处理
    return Promise.reject(error);
});

export default http;