// mock数据模拟
import Mock from 'mockjs'

// 图表数据
let List = [];
export default {
    getStatisticalData: () => {
        for (let i = 0; i < 7; i++) {
            List.push(
                Mock.mock({
                    // Mock.Random.float 随机产生100到8000之间的数，小数位数最小0位最大0位
                    '苹果': Mock.Random.float(100, 8000, 0, 0),
                    'vivo': Mock.Random.float(100, 8000, 0, 0),
                    'oppo': Mock.Random.float(100, 8000, 0, 0),
                    '魅族': Mock.Random.float(100, 8000, 0, 0),
                    '三星': Mock.Random.float(100, 8000, 0, 0),
                    '小米': Mock.Random.float(100, 8000, 0, 0),
                })
            )
        }
        return {
            code: 20000,
            data: {
                //饼图
                videoData: [
                    { name: '小米', value: 2999 },
                    { name: '苹果', value: 5999 },
                    { name: 'vivo', value: 1500 },
                    { name: 'oppo', value: 1999 },
                    { name: '魅族', value: 2200 },
                    { name: '三星', value: 4500 },
                ],
                // 柱状图
                userData: [
                    { date: '周一', new: 5, active: 200 },
                    { date: '周二', new: 10, active: 500 },
                    { date: '周三', new: 12, active: 550 },
                    { date: '周四', new: 60, active: 800 },
                    { date: '周五', new: 65, active: 550 },
                    { date: '周六', new: 53, active: 770 },
                    { date: '周日', new: 33, active: 170 },
                ],
                // 折线图
                orderData: {
                    date: ['20191001', '20191002', '20191003', '20191004', '20191005', '20191006', '20191007'],
                    data: List
                },
                tableData: [
                    { name: "oppo", daySell: 500, monthSell: 3500, allSell: 32000 },
                    { name: "vivo", daySell: 300, monthSell: 2200, allSell: 22000 },
                    { name: "苹果", daySell: 800, monthSell: 4500, allSell: 65000 },
                    { name: "小米", daySell: 1200, monthSell: 5500, allSell: 85000 },
                    { name: "三星", daySell: 300, monthSell: 2000, allSell: 18000 },
                    { name: "魅族", daySell: 350, monthSell: 3000, allSell: 28000 },
                ],
                countData: [
                    {
                        name: "今日支付订单",
                        value: 1234,
                        icon: "success",
                        color: "#2ec7c9",
                    },
                    {
                        name: "今日收藏订单",
                        value: 210,
                        icon: "star-on",
                        color: "#ffb980",
                    },
                    {
                        name: "今日未支付订单",
                        value: 190,
                        icon: "s-goods",
                        color: "#5ab1ef",
                    },
                    {
                        name: "本月支付订单",
                        value: 28790,
                        icon: "success",
                        color: "#2ec7c9",
                    },
                    {
                        name: "本月收藏订单",
                        value: 3219,
                        icon: "star-on",
                        color: "#ffb980",
                    },
                    {
                        name: "本月未支付订单",
                        value: 839,
                        icon: "s-goods",
                        color: "#5ab1ef",
                    },
                ],
            }
        }
    }
}